<script>

    import { slide } from 'svelte/transition'

    import { therapies } from './stores.js';

    import ResultsParser from './ResultsParser.svelte';
    import RemedyCard from './RemedyCard.svelte';

    export var tests;
    export var indentLevel = 0;

    const testsCatalog = [];
    
    function buildCatalog (therapies,indexes,catalog) {
        const therapyIdx = indexes[0];
        var newItem = {}
        var testCatalogTherapyIndex;
        if (indexes.length > 1) {
            newItem['type'] = 'catalog';
            testCatalogTherapyIndex = catalog.findIndex(
                ther=>ther.idx===therapyIdx
            );
            if (testCatalogTherapyIndex === -1) {
                newItem['idx'] = therapyIdx;
                newItem['name'] = therapies[therapyIdx].name;
                newItem['contents'] = [];
                testCatalogTherapyIndex = catalog.push(newItem) - 1;
            }
            const cat = catalog[testCatalogTherapyIndex].contents;
            const idx = indexes.slice(1);
            const ther = therapies[therapyIdx].contents
            buildCatalog(ther,idx,cat);
        } else {
            newItem = therapies[therapyIdx];
            catalog.push(newItem);
        }
    }

    tests.forEach(
        test=>buildCatalog($therapies,test,testsCatalog)
    )

</script>

<div transition:slide>
        {#each testsCatalog as test}
            {#if $therapies[test.idx]}
            <ResultsParser item="{test}" itemComponent="{RemedyCard}"/>
            {:else}
            <p>No tiene disponible la terapeutica con referencia {test.idx}</p>
            {/if}
        {:else}
            <p>No hay remedios seleccionados para esta sesión.</p>
        {/each}
</div>

<style>
    div {
        padding-left: 1em;
    }
</style>