<script>

    import { path, back } from './SvelteNavigate';
    import { therapies } from './stores.js';
    import FloatingButton from './FloatingButton.svelte'

    var remedy = {};

    const therapyId = $path[1];
    const remedyPath = $path.slice(2);

    var level = $therapies[therapyId].clasifications || $therapies[therapyId].remedies;

    remedyPath.forEach(
        idx => {
            level = level[idx].clasifications || level[idx].remedies || level[idx];
        }
    );

    remedy = level;


</script>

<div>
    <div class="data">
        <h1>{remedy.name}</h1>
        {#each remedy.images as image}
        <img src="{image}" alt="{remedy.name} ilustration"/>
        {/each}
        <p>{remedy.description}</p>
        <FloatingButton imgSrc="/img/check.svg" radius="50%" on:click="{back}"/>
    </div>
</div>

<style>
    div.data {
        background-color: white;
        padding: 0em 2em;
    }
    div.data > img {
        max-width: 100%;
    }
</style>