<script>
    import marked from 'marked';
    import { index } from './RemediesIterator.js';
    export var content = 'Give me markdown in "content" prop.';
    export var classes = '';
    export let idx = false;
    const classString = "markdown " + classes;
    if ( idx ) {
        const renderer = {
            heading(text, level, raw, slugger) {
                const id = slugger.slug(raw);
                $index += `${'>'.repeat(level)} [${raw}](#${id})` + '\n\n';
                return `<h${level} id="${id}">${raw}</h${level}>`;
            }
        };
        marked.use({ renderer });
    }
</script>

<div class="{classString}">
    {@html marked(content)}
</div>
