<script>
    import { createEventDispatcher } from 'svelte';
    const dispatch = createEventDispatcher();
    function clickHandler () {
        dispatch("click");
    }
    export var radius = '0.5em';
</script>

<button on:click="{clickHandler}" style="--radius: {radius}">
    <slot>Give me content...</slot>
</button>

<style>
    button {
        background-color: var(--button-color, white);
        height: 3em;
        min-width: 3em;
        border-radius: var(--radius);
        border-style: none;
        color: #2ca089ff;
    }
    button:hover {
        background-color: var(--button-hover-color, grey);
    }
</style>
