<script>

    export var status = false;

    export var color = {
        false:'black',
        true: 'white',
    };
    export var bgColor = {
        false: 'white',
        true: 'grey',
    }

    function clickHandler () {
        status = ! status;
    }

</script>

<button 
    on:click="{clickHandler}" 
    style="
        --col:{color[status]};
        --bgCol:{bgColor[status]};
    "
>
    <slot>Give me any content...</slot>
</button>

<style>
    button {
        color: var(--col);
        background-color: var(--bgCol);
        border-radius: 1.5em;
        height: 3em;
        min-width: 5em;
        margin: 0.25em;
    }
</style>
