<script>
    import { back, path } from './SvelteNavigate.js';

    import RemediesSelector from './RemediesSelector.svelte';
    import TextArea from './TextArea.svelte';
    import FloatingButton from './FloatingButton.svelte';
    import Delete from './Delete.svelte';

    import { patients } from './stores.js';

    var patientId = parseInt($path[1]);
    var sessionId = parseInt($path[2]);
    var patient = $patients[patientId];
    var session = patient.sessions[sessionId];

    function deleteHandler(ev) {
        patient.sessions.splice(ev.detail,1)
        back();
    }

</script>

<div id="header">
    <h2>Sesión {session.date}</h2>
    <Delete radius="50%" value={sessionId} on:delete={deleteHandler}>
        <p slot="title"><strong>¿Borrar la sesión {session.date}?</strong></p>
        <p slot="cancel">No</p>
        <p slot="delete">Sí, borrar</p>
        <img slot="button" id="deleteIcon" src="/img/delete.svg" alt="Borrar sesión"/>
    </Delete>
</div>

<TextArea 
    bind:value="{session.notes}"
    editingTitle="{patient.name} - {session.date} - Notas"
    placeholder="Añade notas a esta sesión..."
/>

<RemediesSelector bind:tests={session.tests}/>

<FloatingButton imgSrc="/img/check.svg" radius="50%" on:click="{back}"/>

<style>
    div#header {
        display: flex;
        justify-content: space-between;
        align-items:baseline;
    }
    img#deleteIcon {
        height: 100%;
    }
</style>
