<script>

    import CatalogParser from './CatalogParser.svelte';
    import { currentTest } from './CatalogParser.js';
    import ToggleButton from './ToggleButton.svelte';
    import { therapies, patients } from './stores.js';

    export var tests;

    if ( tests ) {
        $currentTest = [...tests];
    }

    $: {
        tests = [...$currentTest];
        $patients = [...$patients];
    }

</script>

<div id="container">
{#each Object.entries($therapies) as [therapyId, therapy]}
    <CatalogParser item={therapy} catalogPath={[therapyId]} itemComponent={ToggleButton} tests={tests}/>
{:else}
    <p>No has descargado el catalogo de remedios correspondiente a ninguna terapia.</p>
{/each}
</div>

<style>
    div#container {
        box-sizing: border-box;
    }
</style>