<script>
    import List from './List.svelte';
    import FloatingButton from './FloatingButton.svelte';
    import New from './New.svelte';

    import { patients } from './stores.js';

	import { navigate } from './SvelteNavigate';

    var showNew = false;

    var newPatientName = "";

    function patientHandler(ev) {
        navigate(`/Patient/${ev.detail}`,ev.detail);
    }

    function buttonHandler(ev) {
        showNew = true;
    }

    function handleCancelNew() {
        newPatientName = "";
        showNew = false;
    }

    function handleDoneNew() {
        const name = newPatientName.trim();
        if ( name != "") {
            const newPatient = {
                name,
                notes: '',
                sessions: []
            };
            console.log("Patients:",$patients)
            $patients = [newPatient, ...$patients];
            navigate(`/Patient/${name}`,name);
        } else {
            showNew = false;
        }
    }
</script>

<List list={$patients} showLabel="name" on:click={patientHandler}></List>

<FloatingButton imgSrc="/img/add.svg" radius="50%" on:click="{buttonHandler}"/>

{#if showNew}
<New bind:value={newPatientName} on:cancel="{handleCancelNew}" on:done="{handleDoneNew}">
    <span slot="title">New patient</span>
    <span slot="cancel">Cancel</span>
    <span slot="done">Add</span>
</New>       
{/if}