<script>
    
    import Button from './Button.svelte';
    import { therapies } from './stores.js';
    import { navigate } from './SvelteNavigate.js';

    function therapyButtonHandler(therapyId) {
        navigate(`/Therapy/${therapyId}`,$therapies[therapyId].name)
    }
</script>

<h1>Therapies</h1>
{#each Object.entries($therapies) as [therapyId, therapy]}
    <Button on:click={()=>therapyButtonHandler(therapyId)}>{therapy.name}</Button>
{/each}