<script>
    import Button from './Button.svelte';
    import SessionTests from './SessionTests.svelte';
    import TextArea from './TextArea.svelte';
    import DropDownCard from './DropDownCard.svelte';

    import { patients } from './stores.js'

	import { path, navigate } from './SvelteNavigate';

    const showSessionNotes = {};

    const patientName = $path[1];

    const patientIdx = $patients.findIndex(
        patient=>patient.name===patientName
    )
    const patient = $patients[patientIdx];

    function newSessionHandler() {
        const dateOptions = { weekday: 'short', year: 'numeric', month: 'short', day: 'numeric' };
        const now = new Date(Date.now());
        const nowDateString = now.toLocaleDateString([],dateOptions);
        var todaySessions = 0;
        var dateString = nowDateString;
        while (patient.sessions.findIndex(session=>session.date===dateString) != -1) {
            todaySessions++;
            dateString = `${nowDateString} (${todaySessions})`;
        }
        const newTestsSession = {
            date: dateString,
            notes: "",
            tests: [],
        }
        const testsLength = patient.sessions.push(newTestsSession);
        const sessionIdx = testsLength-1;
        navigate(`/TestsSession/${patientIdx}/${sessionIdx}/`,`Nueva sesión ${newTestsSession.date}`)
    }

    function editSessionHandler(sessionIdx) {
        navigate(`/TestsSession/${patientIdx}/${sessionIdx}/`, patient.sessions[sessionIdx].date)
    }

    function splitParagraphs(str) {
        return str.split(/\r?\n/);
    }

    function storePatients () {
        $patients = [...$patients];
    }

    $: if ( patient.name || patient.notes || patients.sessions) storePatients()


    //if ( ! patient.sessions ) patients.sessions = [];

</script>
<h2>{patient.name}</h2>
<TextArea bind:value={patient.notes} editingTitle="{patient.name}" placeholder="Toca para añadir anotaciones sobre el paciente..."/>

<div id="sessionsTitle">
    <h3>Sesiones:</h3>
    <Button on:click={newSessionHandler}>Nueva sesión</Button>
</div>

<div id="sessionsList">
    {#each patient.sessions as session, idx (session.date)}
    <DropDownCard>
        <img slot="button" src="./img/dropdown.svg" alt="Entrar"/>
        <div slot="top">
            <div id="edit">
                <Button radius="50%" on:click={()=>{editSessionHandler(idx)}}>
                    <img id="editIcon" src="./img/editar.svg" alt="Editar sesión"/>
                </Button>
            </div>
            <div id="headers">
                <h3>{session.date}</h3>
                <div on:click="{()=>showSessionNotes[session.date]=true}">
                    {#each splitParagraphs(session.notes) as paragraph}
                    <p class="notes">{paragraph}</p>
                    {/each}
                </div>
            </div>
        </div>
        <div slot="contents">
            <SessionTests tests={session.tests}/>
        </div>
    </DropDownCard>
    {:else}
    <p>No se han registrado sesiones para este paciente.</p>
    <p>Pulsa el botón para añadir una primera sesión.</p>
    {/each}
</div>

