<script>

    import { path } from './SvelteNavigate.js';
    import Markdown from './Markdown.svelte';
    import RemediesIterator from './RemediesIterator.svelte';
    import Button from './Button.svelte';

    import { index } from './RemediesIterator.js';
    import { therapies } from './stores.js';

    var therapyId;
    var therapy;
    var showIndex = false;

    function indexButtonHandler ()  {
        showIndex = ! showIndex;
    }

    $index = '';
    
    $: {
        therapyId = $path[$path.length-1];
        therapy = $therapies[therapyId];
    }

</script>

{#if $path.includes('Therapy')}
<div id="indexButton">
    <Button on:click={indexButtonHandler}>
        <img id="indexButtonIcon" src="./img/index.svg" alt="índice de terapias"/>
    </Button>
</div>
{/if}

<Markdown content={therapy.notes} classes="notes" idx={true}/>

<div id="contents">
    {#each therapy.contents as item}
        <RemediesIterator item={item}/>
    {/each}
</div>

{#if showIndex}
<div id="index" on:click={indexButtonHandler}>
    <h1>Índice</h1>
    <Markdown content={$index}/>
</div>
{/if}

<style>
    div#index {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: white;
    }
    div#indexButton {
        position:fixed;
        top: 1em;
        right: 1em;
        width: min-content;
    }
    img#indexButtonIcon {
        width: 2em;
    }
</style>