<script>

	// Import components
	import History from './History.svelte';
	import Button from './Button.svelte';

	import SvelteLocalAuth from './SvelteLocalAuth.svelte';

	// Import pages
	import Patients from './Patients.svelte';
	import Patient from './Patient.svelte';
	import Remedy from './Remedy.svelte';
	import TestsSession from './TestsSession.svelte';
	import Therapies from './Therapies.svelte';
	import Therapy from './Therapy.svelte';

	import { path, navigate } from './SvelteNavigate';

	const routes = {
		'': Patients,
		Patients,
		Patient,
		Remedy,
		TestsSession,
		Therapies,
		Therapy,
	}

	function therapiesButtonHandler () {
		navigate('/Therapies/','Terapias');
	}

</script>

<div id="main">
	<SvelteLocalAuth>

		<div id="topBar">
			<History>
				<img src="./img/logo.svg" alt="logo"/>
			</History>
			{#if ! ( $path.includes('Therapies') || $path.includes('Therapy') ) }
			<Button on:click={therapiesButtonHandler}>
				<img src="./img/book.svg" alt="Terapias"/>
			</Button>
			{/if}
		</div>

		<svelte:component this={ routes[$path[0]] }/>

	</SvelteLocalAuth>
</div>

