<script>
    
    import { createEventDispatcher } from 'svelte';
    import { fade } from 'svelte/transition';

    import FloatingButton from "./FloatingButton.svelte";

    const dispatch = createEventDispatcher();

    export var title;
    export var editingTitle = title;
    export var placeholder = 'Introduce tu texto...';
    export var value = '';
    var edit = false;
    var textarea;

    function splitParagraphs(str) {
        return str.split(/\r?\n/);
    }

    $: {
        if (textarea) textarea.focus();
	};

</script>

<div on:click="{()=>{edit=true}}">
    {#if title}
    <em>{title}</em>
    {/if}
    <div id="contents">
    {#if value != ''}
        {#each splitParagraphs(value) as paragraph}
        <p>{paragraph}</p>
        {/each}
    {:else}
        <p id="placeholder">{placeholder}</p>
    {/if}
    </div>
</div>

{#if edit}
<div id="editor" transition:fade="{{duration: 100}}">
    <div id="title">
        {editingTitle}
    </div>
    <textarea bind:value bind:this="{textarea}"></textarea>
    <FloatingButton imgSrc="/img/check.svg" radius="50%" on:click="{()=>edit=false}"/>
</div>
{/if}

<style>
    div#editor {
        z-index: 10;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        display:flex;
        flex-direction: column;
        background-color: white;
    }
    div#editor > div#title {
        width: 100%;
        height: 3em;
        display: flex;
        flex-direction: row;
        align-items: center;
        padding-left: 1em;
        font-size: x-large;
    }
    textarea {
        width: 100%;
        flex-grow: 1;
        font-family: var(--font-family-text);
    }
    p#placeholder {
        color: grey;
    }
    div#contents {
        margin: 1em;
        font-family: var(--font-family-text);
    }
</style>
