import { writable, get } from 'svelte/store';
import { secrets }  from './SvelteLocalAuthLibrary.js'

import { therapy as albaFlowers } from './therapies/albaFlowers.js';
import { therapy as biomagneticPair } from './therapies/biomagneticPair.js';

if ( ! Array.isArray(get(secrets)) ) secrets.set([]);
export const patients = secrets;

export const therapies = writable(
    {
        albaFlowers,
        biomagneticPair,
    }
);
