<script>
    
    import { createEventDispatcher } from 'svelte';
    import { fade } from 'svelte/transition';

    import Button from "./Button.svelte";

    const dispatch = createEventDispatcher();

    export var value;
    export var radius = '';

    var showModal = false;

    function clickHandler() {
        showModal = true;
    }

    function cancel() {
        showModal = false;
    }

</script>

<Button radius={radius} on:click={clickHandler}>
    <slot name="button">Delete</slot>
</Button>
{#if showModal}
<div id="container" on:click|self="{cancel}" transition:fade="{{duration: 100}}">
    <div id="modal">
        <div id="title">
            <slot name="title">Delete?</slot>
        </div>
        <div id=buttons>
            <Button on:click={cancel}>
                <slot name="cancel">Cancel</slot>
            </Button>
            <Button on:click="{()=>dispatch('delete',value)}">
                <slot name="delete">Yes</slot>
            </Button>
        </div>
    </div>
</div>
{/if}
<style>
    div#container {
        z-index: 10;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: rgba(255,255,255,0.7);
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    div#modal {
        background-color: white;
        border-style: solid;
        border-width: 0.5px;
        border-color: rgba(100,100,100,0.5);
        box-shadow: 5px 5px 5px rgba(100,100,100,0.5);
        padding: 1em;
        margin: 2em;
        border-radius: 1.5em;
    }
    div#title {
        margin-bottom: 1em;
    }
    div#buttons {
        display: flex;
        justify-content: space-between;
    }
</style>
