<script>
    import Markdown from './Markdown.svelte';
    import { index } from './RemediesIterator.js';
    export var item;
    export var level = 0;

    $index += `${'>'.repeat(level+2)} [${item.name}](#${encodeURIComponent(item.name)})` + '\n\n';

</script>

<div class='remedyItem'>

    <header id="{encodeURIComponent(item.name)}">{item.name}</header>

    {#if item.type === 'remedy'}
    <Markdown content={item.description} classes="remedyDescription"/>
    {/if}

    {#if item.type === 'catalog'}
    {#each item.contents as subitem}
    <svelte:self item={subitem} level={level+1}/>
    {/each}
    {/if}

</div>

<style>
    div.remedyItem {
        margin-left: 2em;
    }
    header {
        font-weight: bolder;
    }
</style>