<script>

    import { createEventDispatcher } from 'svelte';
    import { slide } from 'svelte/transition'
    import { tweened } from 'svelte/motion';

	const dispatch = createEventDispatcher();

    export var show = false;
    export var eventId = null;
    export var delay = 200;

    const params = {duration: delay}

    const rotation = tweened(0,params);

    function toggle () {
        show = ! show;
        if (show) {
            dispatch('openDropDown', eventId);
            $rotation = -100;
        } else {
            dispatch('closeDropDown', eventId);
            $rotation = 0;
        }
    }

    $: $rotation = show ? -100 : 0;

</script>

<div id="container">
    <div id="top" on:click="{toggle}" >
        <slot name="top">
            <p>Top</p>
        </slot>
        <div id="button" style="--angle:{$rotation}grad;">
            <slot name="button">
                <p>&gt;</p>
            </slot>
        </div>
    </div>
    {#if show}
    <div id="contents" transition:slide="{params}">
        <slot name="contents">
            <p>Contents</p>
        </slot>
    </div>
    {/if}
</div>

<style>
    div#container {
        box-sizing: border-box;
        box-shadow: 3px 6px 10px #2ca089ff;
        position: relative;
        border-radius: 1.5em;
        margin: 0.5em;
        padding: 2em;
        background-color: #2a00080e;
    }
    div#top {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    div#button {
        transform: rotate(var(--angle));
        width: 1.5em;
        height: 1.5em;
    }
</style>
