<script>
    
    import { createEventDispatcher } from 'svelte';
    import { fade } from 'svelte/transition';
    import { onMount } from 'svelte';

    import Button from "./Button.svelte";

    const dispatch = createEventDispatcher();

    export var value;
    var input;

    onMount(() => {
		input.focus();
	});

</script>

<div id="container" on:click|self="{()=>dispatch('cancel')}" transition:fade="{{duration: 100}}">
    <div id="modal">
        <div>
            <slot name="title"></slot>
        </div>
        <input type="text" bind:value bind:this={input}/>
        <Button on:click="{()=>dispatch('cancel')}">
            <slot name="cancel">Cancel</slot>
        </Button>
        <Button on:click="{()=>dispatch('done')}">
            <slot name="done">Done</slot>
        </Button>
    </div>
</div>

<style>
    div#container {
        z-index: 10;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: rgba(255,255,255,0.7);
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    div#modal {
        background-color: white;
        border-style: solid;
        border-width: 0.5px;
        border-color: rgba(100,100,100,0.5);
        box-shadow: 5px 5px 5px rgba(100,100,100,0.5);
        padding: 1em;
        margin: 2em;
        border-radius: 1.5em;
    }
</style>