<script>

    import DropDownCard from './DropDownCard.svelte';

    /** 
     * Needs levelCatalogs for sharing state between recursive instances.
     * Needs currentTest for consolidate state from multiple recursive levels components.
    */
    import { levelCatalogs, currentTest } from './CatalogParser.js';

    export var item = {};
    export var catalogPath;
    export var itemComponent;
    export var deep = 0
    export var tests;

    function openDropDownHandler (ev) {
        var lastOpenedCar = ev.detail;
        for (let cardId in $levelCatalogs[deep]) {
            if (cardId != lastOpenedCar) $levelCatalogs[deep][cardId] = false
        }
    }

    function inArray (containerArray, itemArray) {
        var result = [false];
        containerArray.forEach(
            (path,idx) => {
                if (
                    path.length === itemArray.length
                        &&
                    path.every(
                        (item,idx1)=>itemArray[idx1] === item
                    )
                ) result = [true,idx];
            }
        );
        return result;
    }

    if ( deep === 0 ) {
        $levelCatalogs = [];
        $currentTest = tests || [];
    }

    var status = inArray($currentTest,catalogPath)[0]

    if (item.type === 'catalog') {
        if ($levelCatalogs[deep] === undefined) $levelCatalogs[deep] = {};
        $levelCatalogs[deep][item.name] = false;
    }

    $: {
        if (status && !inArray($currentTest,catalogPath)[0]) {
            $currentTest = [...$currentTest, catalogPath];
        } else if (status === false && inArray($currentTest,catalogPath)[0]){
            var tempArray = [...$currentTest]
            tempArray.splice(inArray(tempArray,catalogPath)[1],1);
            $currentTest = [...tempArray];
        }
    }
    
</script>
{#if item.type === 'catalog'}
<DropDownCard bind:show={$levelCatalogs[deep][item.name]} eventId="{item.name}" on:openDropDown="{openDropDownHandler}">
    <img slot="button" alt="Toggle button" src="./img/dropdown.svg"/>
    <p slot="top"><strong>{item.name}</strong></p>
    <div slot="contents">
        {#each item.contents as contentsItem, idx}
        <svelte:self item="{contentsItem}" catalogPath="{[...catalogPath, idx]}" itemComponent="{itemComponent}" deep="{deep+1}"/>
        {/each}
    </div>
</DropDownCard>
{:else}
<svelte:component this={itemComponent} bind:status={status}>
    {item.name}
</svelte:component>
{/if}

<style>
    div[slot=contents] {
        display: flex;
        flex-wrap: wrap;
        flex-grow: 1;
        justify-content: space-around;
    }
    div[slot=contents] > :global(*){
        flex-grow: 1;
    }
    img[slot=button] {
		width: 100%;
    }
</style>
